      <!-- 精细胞 -->
      <div class="box_icon_spermatid" v-if="showBoxCentricFission && addmover">
        <h2>精细胞</h2>
        <div class="box_spermatid_warp">
          <img src="@/assets/icon_spermatid.png" width="200" alt="" />
          <div class="spermatid_list" :class="{ addmover: addmover }">
            <img
              v-for="item in CentricFissionList_FL1.slice(0, 2)"
