<script>
export default {
  name: "Home",
  data() {
    return {
      showBoxSecondarySpermatocyte: false, //显示次级精母细胞
      showBoxCentricFission: false, //显示着丝分裂
      showBoxSpermatid: false, //显示精细胞
      dragObject: "", //被拖拽元素的数据对象
      //初级精母细胞染色体集合
      chromosomeList: [
        {
          icon: require("@/assets/Atoa/chromosome_blue_aAbb.png"),
          name: "blue_aabb",
          type: "big",
          width: "120",
          tag: 1,
        },
        {
          icon: require("@/assets/chromosome_blue_DD.png"),
          name: "blue_DD",
          type: "small",
          width: "80",
          tag: 2,
        },
        {
          icon: require("@/assets/Atoa/chromosome_red_AaBB.png"),
          name: "red_AABB",
          type: "big",
          width: "120",
          tag: 1,
        },
        {
          icon: require("@/assets/chromosome_red_dd.png"),
          name: "red_dd",
          type: "small",
          width: "80",
          tag: 2,
        },
      ],
      //着丝分裂染色体列表
      CentricFissionList: [
        {
          icon: require("@/assets/Atoa/d_blue_Ab_1.png"),
          parent: "blue_aabb",
          name: "blue_ab_1",
          type: "big",
          width: "120",
          tig: 1,
        },
        {
          icon: require("@/assets/d_blue_ab_2.png"),
          parent: "blue_aabb",
          name: "blue_ab_2",
          type: "big",
          width: "120",
          tig: 3,
        },
        {
          icon: require("@/assets/d_red_AB_1.png"),
          parent: "red_AABB",
          name: "red_AB_1",
          type: "big",
          width: "120",
          tig: 1,
        },
        {
          icon: require("@/assets/Atoa/d_red_aB_2.png"),
          parent: "red_AABB",
          name: "red_AB_2",
          type: "big",
          width: "120",
          tig: 3,
        },
        {
          icon: require("@/assets/d_blue_D_1.png"),
          parent: "blue_DD",
          name: "blue_D_1",
          type: "small",
          width: "80",
          tig: 2,
        },
        {
          icon: require("@/assets/d_blue_D_2.png"),
          parent: "blue_DD",
          name: "blue_D_2",
          type: "small",
          width: "80",
          tig: 4,
        },
        {
          icon: require("@/assets/d_red_d_1.png"),
          parent: "red_dd",
          name: "red_d_1",
          type: "small",
          width: "80",
          tig: 2,
        },
        {
          icon: require("@/assets/d_red_d_2.png"),
          parent: "red_dd",
          name: "red_d_2",
          type: "small",
          width: "80",
          tig: 4,
        },
      ],
      //次级精母细胞1染色体自由组合集合
      ChromosomalFreeCombinationList1: [],
      //次级精母细胞2染色体自由组合集合
      ChromosomalFreeCombinationList2: [],
      //分裂染色体集合1
      CentricFissionList_FL1: [],
      //分裂染色体集合2
      CentricFissionList_FL2: [],
      addMarginBottom: false,
      addmover: false,
    };
  },
  computed: {
    showBoxCentricFissionBtn() {
      //显示着丝分裂按钮
      return (
        this.ChromosomalFreeCombinationList1.length == 2 &&
        this.ChromosomalFreeCombinationList2.length == 2
      );
    },
  },
  methods: {
    resetPage() {
      this.$router.push("/Second");
    },
    toSecond() {
      this.$router.push("/");
    },
    //开始拖拽
    dragStart(e) {
      console.log(1);
      this.dragObject = e.target.dataset.dragobject;
    },
    //拖拽元素在可放置容器中松手
    drog(e) {
      e.preventDefault();
      console.log(3);
      let className = e.target.className;
      if (className == "box_ChromosomalFreeCombination1") {
        console.log(5);
        let listLength = this.ChromosomalFreeCombinationList1.length;
        if (listLength < 2) {
          let dragObject = JSON.parse(this.dragObject);
          if (listLength == 0) {
            this.ChromosomalFreeCombinationList1.push(dragObject);
          } else {
            let tempList = this.ChromosomalFreeCombinationList1.filter(
              (item) => {
                return (
                  item.name == dragObject.name || item.type == dragObject.type
                );
              }
            );
            if (tempList.length <= 0) {
              this.ChromosomalFreeCombinationList1.push(dragObject);
            }
          }
        }
      } else if (className == "box_ChromosomalFreeCombination2") {
        console.log(5);
        let listLength = this.ChromosomalFreeCombinationList2.length;
        if (listLength < 2) {
          let dragObject = JSON.parse(this.dragObject);
          if (listLength == 0) {
            this.ChromosomalFreeCombinationList2.push(dragObject);
          } else {
            let tempList = this.ChromosomalFreeCombinationList2.filter(
              (item) => {
                return (
                  item.name == dragObject.name || item.type == dragObject.type
                );
              }
            );
            if (tempList.length <= 0) {
              this.ChromosomalFreeCombinationList2.push(dragObject);
            }
          }
        }
      } else if (className == "item") {
        //...
      }
    },
    //拖拽元素进入可放置容器中100ms触发一次
    dragOver(e) {
      e.preventDefault();
    },
    handleCentricFission() {
      this.showBoxCentricFission = true;
      setTimeout(() => {
        let parentNameList1 = [];
        let parentNameList2 = [];
        this.ChromosomalFreeCombinationList1.forEach((item, index) => {
          parentNameList1.push(item.name);
          parentNameList2.push(
            this.ChromosomalFreeCombinationList2[index].name
          );
        });
        let tempCentricFissionList_FL1 = this.CentricFissionList.filter(
          (item) => {
            return (
              item.parent == parentNameList1[0] ||
              item.parent == parentNameList1[1]
            );
          }
        );
        let tempCentricFissionList_FL2 = this.CentricFissionList.filter(
          (item) => {
            return (
              item.parent == parentNameList2[0] ||
              item.parent == parentNameList2[1]
            );
          }
        );
        this.CentricFissionList_FL1 = tempCentricFissionList_FL1.sort(
          (a, b) => {
            return a.tig - b.tig;
          }
        );
        this.CentricFissionList_FL2 = tempCentricFissionList_FL2.sort(
          (a, b) => {
            return a.tig - b.tig;
          }
        );
        setTimeout(() => {
          this.addMarginBottom = true;
          setTimeout(() => {
            this.addmover = true;
          }, 3000);
        }, 600);
      }, 1000);
    },
  },
};
</script>
