<template>
  <div class="page">
    <div class="title">
      <p>
        第二关：互换（同源染色体的非姐妹染色单体） +
        自由组合（非同源染色体）减数分裂推演
      </p>
      <div>
        <el-button type="danger" @click="resetPage">重新开始</el-button>
        <el-button type="success" @click="toSecond">进入第一关</el-button>
      </div>
    </div>
    <div class="home">
      <!-- 初级精母细胞 -->
      <div class="box_primary_spermatocyte">
        <!-- 初级精母细胞主体-->
        <div class="main">
          <h2>初级精母细胞</h2>
          <div class="box_warp">
            <img
              src="@/assets/icon_primary_spermatocyte.png"
              width="260"
              alt=""
            />
            <div class="box_chromosome">
              <img
                v-for="item in chromosomeList"
                :key="item.name"
                :src="item.icon"
                :width="item.width - 16"
                alt=""
              />
            </div>
          </div>
        </div>
        <!-- 开始提示 -->
        <div class="tips" v-if="!showBoxSecondarySpermatocyte">
          <el-card shadow="always">
            <p>请选择你想推演分析的交叉互换情况</p>
            <el-button
              type="warning"
              @click="handleA_exchange_a"
              :disabled="toPath != ''"
              >A与a互换</el-button
            >
            <el-button
              type="warning"
              @click="handleB_exchange_b"
              :disabled="toPath != ''"
              >B与b互换</el-button
            >
            <el-button
              type="danger"
              @click="handleExchangeReset"
              :disabled="toPath == ''"
              >重置选择</el-button
            >
          </el-card>
          <div
            class="tips"
            v-if="!showBoxSecondarySpermatocyte && toPath != ''"
          >
            <el-card shadow="always">
              <p>你已选择：{{ exchangeText }}</p>
              <el-button
                type="primary"
                @click="toStart"
                :disabled="toPath == ''"
                >进入推演</el-button
              >
            </el-card>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      //初级精母细胞染色体集合
      chromosomeList: [
        {
          icon: require("@/assets/chromosome_blue_aabb.png"),
          name: "blue_aabb",
          type: "big",
          width: "120",
          tag: 1,
        },
        {
          icon: require("@/assets/chromosome_blue_DD.png"),
          name: "blue_DD",
          type: "small",
          width: "80",
          tag: 2,
        },
        {
          icon: require("@/assets/chromosome_red_AABB.png"),
          name: "red_AABB",
          type: "big",
          width: "120",
          tag: 1,
        },
        {
          icon: require("@/assets/chromosome_red_dd.png"),
          name: "red_dd",
          type: "small",
          width: "80",
          tag: 2,
        },
      ],
      toPath: "",
      exchangeText: "",
    };
  },
  methods: {
    resetPage() {
      this.$router.go(0);
    },
    toSecond() {
      this.$router.push("/");
    },
    toStart() {
      this.$router.push(this.toPath);
    },
    handleA_exchange_a() {
      this.exchangeText = "A与a互换";
      this.chromosomeList.forEach((item) => {
        if (item.name == "blue_aabb") {
          this.$set(
            item,
            "icon",
            require("@/assets/Atoa/chromosome_blue_aAbb.png")
          );
        }
        if (item.name == "red_AABB") {
          this.$set(
            item,
            "icon",
            require("@/assets/Atoa/chromosome_red_AaBB.png")
          );
        }
      });
      this.toPath = "/A_exchange_a";
    },
    handleB_exchange_b() {
      this.chromosomeList.forEach((item) => {
        if (item.name == "blue_aabb") {
          this.$set(
            item,
            "icon",
            require("@/assets/Btob/chromosome_blue_aabB.png")
          );
        }
        if (item.name == "red_AABB") {
          this.$set(
            item,
            "icon",
            require("@/assets/Btob/chromosome_red_AABb.png")
          );
        }
      });
      this.toPath = "/B_exchange_b";
      this.exchangeText = "B与b互换";
    },
    handleExchangeReset() {
      this.chromosomeList.forEach((item) => {
        if (item.name == "blue_aabb") {
          this.$set(item, "icon", require("@/assets/chromosome_blue_aabb.png"));
        }
        if (item.name == "red_AABB") {
          this.$set(item, "icon", require("@/assets/chromosome_red_AABB.png"));
        }
      });
      this.toPath = "";
    },
  },
};
</script>
