import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import A_exchange_a from '../views/A_exchange_a.vue'
import B_exchange_b from '../views/B_exchange_b.vue'
import Second from '../views/Second.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/Second',
    name: 'Second',
    component: Second
  },
  {
    path: '/A_exchange_a',
    name: 'A_exchange_a',
    component: A_exchange_a
  },
  {
    path: '/B_exchange_b',
    name: 'B_exchange_b',
    component: B_exchange_b
  },
]

const router = new VueRouter({
  routes
})

export default router
